/* grid vars */
$gutter: 20px;
$screen-xs: 0;
$screen-sm: 768px;
$screen-md: 1024px;
$screen-lg: 1280px;

$screen-xs-container: 360px;
$screen-sm-container: 720px;
$screen-md-container: 1000px;
$screen-lg-container: 1060px;

/*

экран >1280     => контент 1000px
экран 1279-1024 => контент 960px
экран 1023-768  => контент 680px
экран <768      => контент 100% минус гаттеры

*/

@media (min-width: $screen-xs) {
  body {
    min-width: 320px;
  }
}
@media (min-width: $screen-sm) {
  body {
    min-width: $screen-sm-container;
  }
}
@media (min-width: $screen-md) {
  body {
    min-width: $screen-md-container;
  }
}
@media (min-width: $screen-lg) {
  body {
    min-width: $screen-lg-container;
  }
}


/* -------------------------------------------------- */

@media(max-width: 767px) {
  .ninja--xs   { display: none  !important; }
}
@media(min-width: 768px) and (max-width: 1023px) {
  .ninja--sm   { display: none  !important; }
}
@media(min-width: 1024px) and (max-width: 1279px) {
  .ninja--md   { display: none  !important; }
}
@media(min-width: 1279px) and (max-width: 1439px)  {
  .ninja--lg   { display: none  !important; }
}
@media(min-width: 1440px) {
  .ninja--xl   { display: none  !important; }
}

/* -------------------------------------------------- */

@media(max-width: 767px) {
  .ninja--mobile  { display: none  !important; }
}
@media(min-width: 768px) {
  .ninja--desktop { display: none  !important; }
}

/* -------------------------------------------------- */

/* container */
.grid-container {
  margin-right: auto;
  margin-left: auto;
  padding-left: calc($gutter/2);
  padding-right: calc($gutter/2);
}

/* -------------------------------------------------- */

/* row */
.grid-row {
  margin-left: calc($gutter / 2);
  margin-right: calc($gutter / 2);
}

