/* clearfix */
.grid-container:before,
.grid-container:after,
.grid-container--fluid:before,
.grid-container--fluid:after,
.grid-row:before,
.grid-row:after {
  content: " ";
  display: table;
}
.grid-container:after,
.grid-container--fluid:after,
.grid-row:after {
  clear: both;
}
